.dashboardPage {
  height: 100vh;
  width: 100vw;
  display: flex;
  background-color: rgb(234, 234, 234);
}

table {
  table-layout: fixed;
}

.sidebarContainer {
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  padding-left: 15px;
  background-color: rgb(32, 40, 134);
  justify-content: space-between;
  padding-bottom: 70px;
}

.navLinks {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.navButton {
  height: 30px;
  font-size: 1.4rem;
  padding: 8px 0px 8px 0px;
  font-style: none;
  border-radius: 7px 0 0 7px;
}

a.navButton {
  transition: transform 250ms;
}

a:link.navButton {
  color: rgb(237, 237, 237);
}

a:visited.navButton {
  color: rgb(237, 237, 237);
}

a:hover.navButton {
  color: rgb(81, 112, 204);
  transform: translateY(-2px);
}

.miscButtonsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

.miscButtons {
  color: rgb(237, 237, 237);
  transition: transform 250ms;
}

.miscButtons:hover {
  color: rgb(81, 112, 204);
  transform: translateY(-2px);
  cursor: pointer;
}

.actionButton {
  color: rgb(42, 42, 42);
  padding: 0 15px;
}

.actionButton:hover {
  color: rgb(81, 112, 204);
  cursor: pointer;
}

.active {
  background-color: rgb(182, 185, 225);
}

.inactive {
  background-color: rgb(32, 40, 134);
}

.sidebarIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 20px 0 5px;
}

.dashboardInfoContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dashboardInfoPage {
  background-color: rgb(245, 245, 245);
  height: calc(100vh - 30px);
  width: calc(100vw - 110px);
  border-radius: 8px;
  box-shadow: 2px 2px 10px 1px rgb(170, 170, 170);
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.dashboardInfoHeader {
  display: flex;
  border-bottom: 3px solid #edf0f1;
  justify-content: space-between;
}

h3.infoPageTitle {
  padding: 0;
  margin: 0;
  padding: 20px 0 20px 0;
  font-size: 1.7rem;
  color: rgb(68, 68, 86);
}

.infoPagePadding {
  padding: 5px 20px 5px 20px;
}

.tablePadding {
}

.searchbar {
  padding: 9px 5px 9px 5px;
  border-bottom: 3px solid #edf0f1;
}

#searchBar {
  width: 300px;
  height: 1.3rem;
  border: 2px solid #d1d2d3;
  border-radius: 4px;
  font-size: 0.9rem;
  background-color: #f1f4f7;
}

.dashboardButtons {
  display: flex;
  align-items: center;
}

.addNewButton {
  border: none;
  background-color: rgb(32, 40, 134);
  color: white;
  font-size: 1rem;
  padding: 10px 8px 10px 8px;
  border-radius: 6px;
}

.addNewButton:hover {
  background-color: rgb(82, 91, 182);
  cursor: pointer;
}

table {
  border-collapse: collapse;
  width: 100%;
}

thead {
  padding: 10px 7px 10px 7px;
  border-bottom: 3px solid #edf0f1;
}

th {
  padding: 5px;
  font-weight: 400;
  color: rgb(75, 86, 199);
}

td {
  padding: 10px 0 10px 0;
  text-align: center;
  border-top: 2px solid #edf0f1;
}

tbody > tr:hover {
  background-color: whitesmoke;
}

input#searchBar {
  text-indent: 10px;
}

#infoWarning {
  font-size: 0.8rem;
  color: rgb(248, 63, 63);
}
